.about{
  margin: 30px 0px;
  padding: 20px;
  margin-left: 200px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  border-radius: 8px;
}

.about-img img{
    border-radius:50%;
    opacity: 100%;
    width: 76%; 
    height: 400px; 
}

.about-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-content h1{
  color: gray;
  font-family: Gill Sans Extrabold;
  border-bottom: 2px solid gray;
  text-align: center;
}

.about-content p{
    text-align: justify;
    padding: 20px;
}

@media(max-width:600px){
  .about{
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }

  .about-content h1{
    margin-top: 10px;
  }
}