.home-container{
  height: 300px;
  color: white;
  background-color: black;
  padding-left: 120px;
}

.home-content h2{
    padding: 5px;
    padding-top: 70px;
}

.home-content h1{
    color: #e0b50f;
    padding: 5px;  

}

.home-buttons {
   margin-top: 20px
  }

.home-buttons .btn-hire{
  background-color: #3b8ff3;
  color: white;
  width: 160px
}

.home-buttons .btn-hire:hover{
    background-color: white;
    color: #3b8ff3;
    width: 200px
  }

  .home-buttons .btn-cv{
    background-color: #f29f67;
    color: white;
    margin-left: 15px;
  }
  
  .home-buttons .btn-cv:hover{
   opacity: 80%;
   transition: 1s;
  }

