.education {
    margin: 50px 0px;
    margin-left: 135px;
    background-color: rgba(128, 128, 128, 0.103);
    padding:100px 0px;
}

.education hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}

.education h2{
    font-weight: bold;
    color:#138781
}

.education .vertical-timeline-element-title{
    color: #138781;
}

.education .vertical-timeline-element-subtitle{
    color: #1e1e2c87;
}

@media(max-width:600px){
    .education{
      margin: 0;
      padding: 0;
      margin-top: 30px;
    }
  
    .education h2{
      padding-top: 20px;
    }

    .education hr{
        width: 280px;
        margin-bottom: 10px;
    }
  }